	import { sendAjax } from '@/common/js/sendAjax.js';
	import config from '@/apiConfig';
	const {getCleanerDeail,getCleanerWork} = config.api;
export default {
	data() {
		return {
			id:null,
			basicsList: [{name: '预约时间'},{name: '完善信息'},{name: '估价'}],
			// 0 预约时间 1 预约时间+完善信息 2  预约时间+完善信息+提交
			scroll: 0,
			detail:'',
			chooseType:'',
			chooseData:'',
			dataList: [],
			yuyueList:{}
		};
	},
	methods: {
		onLoad: function(e) {
			console.log(e)
			this.id = e.id;
			this.getCleanerDeail();
			this.getWorkTime();
			this.getCleanerWork();
		},
		getCleanerDeail(){
			var that = this;
			let infoOpt = {
				url: getCleanerDeail,
				type: 'POST',
				data: {
					id:this.id
				}
			};
			let infoCb = {};
			infoCb.success = function(res) {
				console.log(res)
				that.detail = res;
				uni.hideLoading()
			},
			infoCb.beforeSend = () => {
			  uni.showLoading({
			  	title:'加载中'
			  })
			};
			sendAjax(infoOpt, infoCb);
		},
		getCleanerWork(){
			var that = this;
			let infoOpt = {
				url: getCleanerWork,
				type: 'POST',
				data: {
					cleanerId:this.id
				}
			};
			let infoCb = {};
			infoCb.success = function(res) {
				var workList = res;
				for(var i in workList){
					workList[i].workDate = workList[i].workDate.substring(5,10)
				}
				var yuyueList = {}
				for(var i in workList){
					yuyueList[workList[i].workDate] = workList[i].statusMor.toString() + workList[i].statusAft.toString()
				}
				console.log(yuyueList)
				that.yuyueList = yuyueList
				uni.hideLoading()
			},
			infoCb.beforeSend = () => {
			  uni.showLoading({
			  	title:'加载中'
			  })
			};
			sendAjax(infoOpt, infoCb);
		},
		getWorkTime(){
			var arr = []
			for (let i = 0; i < 7; i++) {
			  arr.push(this.dealTime(i))
			}
			console.log(arr)
			this.dataList = arr;
		},
		// 处理未来七天的函数
		  dealTime: function (num) {     // num：未来天数
			var time = new Date()     // 获取当前时间日期
		    var date = new Date(time.setDate(time.getDate() + num + 1)).getDate()  //这里先获取日期，在按需求设置日期，最后获取需要的
			if(date<10) date = '0' + date;
		    var month = time.getMonth() + 1   // 获取月份
		    var day = time.getDay()   //  获取星期
		    switch (day){             //  格式化
		      case 0: day = "周日"
		        break
		      case 1: day = "周一" 
		        break
		      case 2: day = "周二"
		        break
		      case 3: day = "周三"
		        break
		      case 4: day = "周四" 
		        break
		      case 5: day = "周五"
		        break
		      case 6: day = "周六"
		        break
		    }
		    var obj = {
		      workDate: month + '-' + date,
		      workTime: day,
			  statusMor:0,
			  statusAft:0
		    }
		    return obj      // 返回对象
		  },

		//点击时间预约
		yuyue(e){
			console.log(e)
			var type = e.currentTarget.dataset.type;
			var data = e.currentTarget.dataset.data;
			this.chooseType = type;
			this.chooseData = data;
		},
		//拨打电话
		phone(e){
			console.log(e)
			uni.makePhoneCall({
			  phoneNumber: e.currentTarget.dataset.phone
			})
		},
		//下一页
		toSub() {
			var id = this.id;
			var day = this.chooseData;
			var type = this.chooseType;
			
			uni.navigateTo({
				url: '../subscribeSub/subscribeSub?day=' + day +'&type=' + type +'&id='+id
			});
		}
	}
};
